/* eslint-disable prefer-destructuring */
import React, { useState, useEffect, useMemo } from 'react'
import styled from 'styled-components'
import queryString from 'query-string'
import { useLocation } from 'react-router-dom'
import { animated, useTransition, useSpringRef } from 'react-spring'
import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import LoaderIcon from 'components/LoaderIcon'
import useOpenPackage from 'hooks/useOpenPackage'

const Wrapper = styled(Flex)`
  max-width: 1000px;
  margin: 0 auto;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  background-image: url('/images/bg-opening.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  min-height: inherit;
  padding: 180px 0px;
`

const PackageFrame = styled(Box)`
  width: 100%;
  position: relative;
  margin-bottom: 20px;
  background-image: url(${(props) => props.url});
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 225px;
`
const PackageImage = styled.img`
  width: 140px;
  height: 138px;
  margin-top: 10px;
  margin-bottom: 15px;
`

const StyledButton = styled(Flex)`
  align-items: center;
  justify-content: center;
  background-image: url('/images/checkout-button.png');
  background-repeat: no-repeat;
  background-size: contain;
  height: 60px;
  width: 279px;
  margin: 0 auto;
  background-position: center;
  transition: 0.2s all ease-in;

  &:hover {
    cursor: pointer;
    transform: translateY(-2px);
  }
`
const StyledText = styled(Text)`
  text-shadow: 0 -1px 2px #ce2d90, 0 -2px 5px #ce2d90, 0 -10px 70px #ce2d90, 0 -18px 40px #ce2d90;
  font-weight: 900;
`
const StyledPackText = styled(Text)`
  font-size: 1.6rem;
  font-weight: 900;
  color: #ffffff;
`

const Package = ({ onHandleOpenPackage }) => {
  return (
    <>
      <Flex mb="50px">
        <PackageFrame url="/images/inventory-frame-common.png">
          <Flex flexDirection="column" justifyContent="center" pt="25px" pb="25px" height="100%" alignItems="center">
            <StyledPackText>Rare pack</StyledPackText>
            <PackageImage src="/images/common.png" />
          </Flex>
        </PackageFrame>
      </Flex>
      <StyledButton>
        <StyledText fontSize="2rem" color="#ffffff" onClick={onHandleOpenPackage}>
          Click to open
        </StyledText>
      </StyledButton>
    </>
  )
}

const defaultPages = [
  ({ style, onHandleOpenPackage }) => (
    <animated.div style={{ ...style }}>
      <Package onHandleOpenPackage={onHandleOpenPackage} />
    </animated.div>
  ),
]

const PackageOpening = ({ packageData }) => {
  console.log(packageData, 'data ?')
  const [pages, setPages] = useState(defaultPages)
  const { onUnpack } = useOpenPackage()
  const location = useLocation()
  const queries = useMemo(() => {
    return queryString.parse(location.search, { arrayFormat: 'bracket' })
  }, [location.search])
  const transRef = useSpringRef()
  /* Config for transition */
  const transitions = useTransition(pages, {
    ref: transRef,
    keys: null,
    from: { opacity: 0, position: 'absolute', transform: 'translateY(-100px)' },
    enter: (item) => async (next, cancel) => {
      await next({ opacity: 1, position: 'unset' })
      await next({ transform: 'translateY(0px)' })
    },
    leave: (item) => async (next, cancel) => {
      await next({ opacity: 0, position: 'absolute', transform: 'translateY(-100px)' })
    },
    config: { duration: 500 },
    expires: true,
    trail: 2000,
  })

  const onHandleOpenPackage = async () => {
    const { packageIndex } = queries
    const packages = packageIndex.map((index) => {
      return [
        Number(index),
        [
          [1, 2, 2, 1, '120398102938019238098'],
          [1, 0, 1, 1, '838293481723'],
          [1, 1, 0, 1, '584930802374'],
        ],
      ]
    })
    setPages((state) => [({ style }) => <LoaderIcon />])
    const result = await onUnpack(packages)
    setPages((state) => [
      ({ style }) => (
        <animated.div style={{ ...style, width: '279px' }}>
          <Flex mb="50px">
            <PackageFrame url="/images/frame-opening.png">
              <Flex
                flexDirection="column"
                justifyContent="center"
                pt="25px"
                pb="25px"
                height="100%"
                alignItems="center"
              >
                <StyledPackText>{1}</StyledPackText>
                <PackageImage src="/images/common.png" />
              </Flex>
            </PackageFrame>
          </Flex>
        </animated.div>
      ),
      ({ style }) => (
        <animated.div style={{ ...style, width: '279px' }}>
          <Flex mb="50px">
            <PackageFrame url="/images/frame-opening.png">
              <Flex
                flexDirection="column"
                justifyContent="center"
                pt="25px"
                pb="25px"
                height="100%"
                alignItems="center"
              >
                <StyledPackText>{1}</StyledPackText>
                <PackageImage src="/images/common.png" />
              </Flex>
            </PackageFrame>
          </Flex>
        </animated.div>
      ),
      ({ style }) => (
        <animated.div style={{ ...style, width: '279px' }}>
          <Flex mb="50px">
            <PackageFrame url="/images/frame-opening.png">
              <Flex
                flexDirection="column"
                justifyContent="center"
                pt="25px"
                pb="25px"
                height="100%"
                alignItems="center"
              >
                <StyledPackText>{1}</StyledPackText>
                <PackageImage src="/images/common.png" />
              </Flex>
            </PackageFrame>
          </Flex>
        </animated.div>
      ),
    ])
  }

  useEffect(() => {
    transRef.start()
  }, [pages])

  return (
    <Wrapper>
      {transitions((style, Page) => {
        return (
          <animated.div style={{ ...style }}>
            <Page
              onHandleOpenPackage={() => {
                onHandleOpenPackage()
              }}
            />
          </animated.div>
        )
      })}
    </Wrapper>
  )
}

export default PackageOpening
