import React from 'react'
import { Switch, Route, useRouteMatch } from 'react-router-dom'
import Stash from './Stash'
import PackageOpening from './PackageOpening'
import useGetInventoryData from './hooks/useGetInventoryData'

const Inventory = () => {
  const { path } = useRouteMatch()
  const data = useGetInventoryData()
  return (
    <Switch>
      <Route path={`${path}/package-opening/:packageType`}>
        <PackageOpening packageData={data} />
      </Route>
      <Route path={`${path}`}>
        <Stash packageData={data} />
      </Route>
    </Switch>
  )
}

export default Inventory
