import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { getPackageIndex } from 'utils/callHelpers'
import useRefresh from 'hooks/useRefresh'
import { useCyblockPresaleContract } from '../../../hooks/useContract'
import { filterPackageIndexes } from '../utils/formatPackageCount'

const DEFAULT_DATA = {
  commonPackageData: [],
  rarePackageData: [],
  epicPackageData: [],
}

const useGetInventoryData = () => {
  const { account } = useWeb3React()
  const [data, setData] = useState(DEFAULT_DATA)
  const { slowRefresh, fastRefresh } = useRefresh()
  const commonPackageContract = useCyblockPresaleContract('0x935530720e8f1D2D43a790Fe5E4B0466aE531931')
  const rarePackageContract = useCyblockPresaleContract('0x935530720e8f1D2D43a790Fe5E4B0466aE531931')
  const epicPackageContract = useCyblockPresaleContract('0x935530720e8f1D2D43a790Fe5E4B0466aE531931')
  const getNumberOfUserPackage = async (packageContract) => {
    try {
      const userPackIndex = await packageContract.methods.userPackIndexs(account).call()
      return userPackIndex
    } catch (error) {
      console.log(error, 'Fail to fetch package data')
      return []
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const numberOfCommonPackage = await getNumberOfUserPackage(commonPackageContract)
        const numberOfRarePackage = await getNumberOfUserPackage(rarePackageContract)
        const numberOfEpicPackage = await getNumberOfUserPackage(epicPackageContract)
        setData({
          ...data,
          commonPackageData: numberOfCommonPackage,
          rarePackageData: numberOfRarePackage,
          epicPackageData: numberOfEpicPackage,
        })
      } catch (error) {
        console.log(error, 'error')
      }
    }
    if (account) {
      fetchData()
    }
  }, [account, fastRefresh])

  return data
}

export default useGetInventoryData
